        <el-button @click="dialogVisible.subCourseStudent = false" size="small">取消</el-button>
      </span>
    </el-dialog>
    <el-dialog
      title="人员对应课程明细"
      :visible.sync="dialogVisible.employeeDetail"
      width="50%">
      <div>
          <el-row :gutter="1">
            <el-col :span="5">
              <div>
                <el-statistic
                  :value="employeeDetailObj.count.allCount"
                  title="应到数"
                ></el-statistic>
              </div>
            </el-col>
            <el-col :span="5">
              <div>
                <el-statistic title="实到数" :value="employeeDetailObj.count.callon">
                </el-statistic>
              </div>
            </el-col>
            <el-col :span="5">
              <div>
                <el-statistic title="未到数" :value="employeeDetailObj.count.calloff">
                </el-statistic>
              </div>
            </el-col>
            <el-col :span="5">
              <div>
               <el-statistic title="缴费数" :value="employeeDetailObj.count.isMoneyOn">
               </el-statistic>
              </div>
            </el-col>
            <el-col :span="4">
              <div>
               <el-statistic title="未缴费" :value="employeeDetailObj.count.isMoneyOff">
               </el-statistic>
              </div>
            </el-col>
          </el-row>
        </div>
      <el-table :data="employeeDetailObj.detail" >
        <el-table-column prop="name" label="课程名" width="80">
        </el-table-column>
        <el-table-column prop="courseDescription" label="课程备注" width="150">
        </el-table-column>
        <el-table-column prop="description" label="学生备注" width="150">
        </el-table-column>
        <el-table-column prop="isClass" label="是否上课" width="80">
        </el-table-column>
        <el-table-column prop="description" label="上课内容" width="150">
        </el-table-column>
        <el-table-column prop="createTime" label="上课时间" width="150">
        </el-table-column>
        <el-table-column prop="isDelete" label="是否删除" width="80">
        </el-table-column>
        <el-table-column prop="isMoney" label="是否付费" width="80">
        </el-table-column>
      </el-table>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible.employeeDetail = false" size="small">取消</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
  import axios from 'axios'
export default {
  name: 'HelloWorld',
  data () {
    return {
      hosturl: "http://1.94.236.227:8080/",
      tableData: [],
      tableDialog:{
        RelationData:[]
      },
      index:1,
      isCollapse: false,
      dialogVisible:{
        employeeAdd:false,
        employeeUpdate:false,
        employeeRelation:false,
        employeeRelationAdd:false,
        courseAdd:false,
        courseUpdate:false,
        employeeCourseAdd:false,
        callStudent:false,
        subCourse:false,
        subCourseEdit:false,
        subCourseStudent:false,
        employeeDetail:false
      },
      AllEmployeeQuery: {
          page: 0,
          grade: "",
          name: ""
      },
      TabeleEntityList:[{
          id: 1,
          typeFlag: "",
          keyword: "",
          valueword: "",
          isDelete: 0
      }],
      EmployeeEntity:{
        id: 1,
        pid: 0,
        pidName:'',
        name: "",
        grade: "",
        phone: "",
        img: "",
        description: ""
      },
      AllCourseQuery:{
        DayPeriad:""
      },
      CourseEntity:{
        id: 1,
        pid: 0,
        name: "",
        startTime: "",
        endTime: "",
        dayPeriad: "",
        validTime: "",
        img: "",
        description: "",
        isDelete:0,
        price:200,
      },
      EmployeeCourseAddDialogModel:{
        employeeData:[],
        value:[],
        currentValue:[],
        selectEmployeeData:[],
        selectedEmployee:{},
        tableData:[],
        subCourseData:[],
        subCourseEntity:{

        },
        subCourseStudentData:[]
      },
      StudentClassEntity:{
        id: 1,
        studentID: 0,
        courseID: 1,
        coursePID: 1,
        isClass: "",
        description: "",
        isDelete: "",
        img: ""
      },
      employeeDetailObj:{
        detail:[],
        count:{}
      }
    }
  },
    methods: {
      subCourseEdit(row){
        this.EmployeeCourseAddDialogModel.subCourseEntity = row;
        this.dialogVisible.subCourseEdit = true;
      },
      UpdateSubCourse(){
        this.Post("UpdateSubCourse",this.EmployeeCourseAddDialogModel.subCourseEntity,response =>{
            this.dialogVisible.subCourseEdit = false;
            this.QueryCourseByPIDShow(this.CourseEntity);
        });
      },
      QuerySubCourseByStudent(row){
        this.EmployeeEntity=row;
        this.Post("QuerySubCourseByStudent",this.EmployeeEntity,response =>{
          console.log(response.data.data)
          this.dialogVisible.employeeDetail = true;
          this.employeeDetailObj=response.data.data;
        });
      },
      QuerySubCourseStudent(row){
        this.EmployeeCourseAddDialogModel.subCourseEntity = row;
        this.Post("QuerySubCourseStudent",this.EmployeeCourseAddDialogModel.subCourseEntity,response =>{
          this.dialogVisible.subCourseStudent = true;
          this.EmployeeCourseAddDialogModel.subCourseStudentData=response.data.data;
        });
      },
      QueryCourseByPIDShow(row){
        this.CourseEntity = row;
        this.Post("QueryCourseByPID?pid="+row.id,{},response =>{
            this.dialogVisible.subCourse = true;
            this.EmployeeCourseAddDialogModel.subCourseData = response.data.data;
        });
      },
      callStudentInsert(){
        if(this.EmployeeCourseAddDialogModel.tableData.find(i=> i.studentID == this.EmployeeCourseAddDialogModel.selectedEmployee)){
          this.$message({
                    message: '此人在课上，请添加新人',
                    type: 'success'
                  });
                  return;
        }
        let student = {
        id: Math.floor(Math.random()*1000),
        studentID: 0,
        courseID: 1,
        coursePID: 1,
        isClass: "否",
        description: "",
        isDelete: "否",
        img: ""
      }
      let item = this.EmployeeCourseAddDialogModel.selectEmployeeData.find(i => i.value == this.EmployeeCourseAddDialogModel.selectedEmployee);
      student.studentID = item.value;
      student.studentName = item.label;
      this.EmployeeCourseAddDialogModel.tableData.push(student);
      console.log(this.EmployeeCourseAddDialogModel.selectedEmployee)
      },
      callStudentMethod(){
        this.$prompt('请输入此节课备注', '提示', {
                  confirmButtonText: '确定',
                  cancelButtonText: '取消'
                }).then(({ value }) => {
                  let course ={
        id: 1,
        pid: 0,
        name: "",
        startTime: "",
        endTime: "",
        dayPeriad: "",
        validTime: "",
        img: "",
        description: "",
        isDelete:0,
        price:200,
      };
      course.pid = this.CourseEntity.id;
      course.name = value? value:this.CourseEntity.name;
      course.description = this.CourseEntity.description;
      course.isDelete = this.CourseEntity.isDelete;
      course.price = this.CourseEntity.price;
      let obj ={
        course:course,
        StudentClass:this.EmployeeCourseAddDialogModel.tableData
      }
                  axios.post(this.hosturl+"api/CallStudent",obj, {
                    headers: {
                      'Content-Type': 'application/json',
                    },
                  }).then(response => {
                    this.$message({
                              message: '点名成功',
                              type: 'success'
                            });
                            this.dialogVisible.callStudent = false;
                  })
                  .catch(error => {
                    console.error(error);
                  });
                }).catch(() => {
                  this.$message({
                    type: 'info',
                    message: '取消输入'
                  });
                });
      },
      editStudentCourse(row){
        this.$prompt('请输入备注', '提示', {
                  confirmButtonText: '确定',
                  cancelButtonText: '取消'
                }).then(({ value }) => {
                  row.description = value
                }).catch(() => {
                  this.$message({
                    type: 'info',
                    message: '取消输入'
                  });
                });
      },
      CallStudentDialogMethod(entity){
        this.CourseEntity = entity;
        this.dialogVisible.callStudent=true;
        this.EmployeeCourseAddDialog(1);
      },
      handleSelectionChange(val){
        let current = [];
        val.forEach(i => {
          current.push(i.id);
        })
        this.EmployeeCourseAddDialogModel.tableData.forEach(item =>{
          if(current.find(i => i == item.id)){
            item.isClass = "是";
          }else{
            item.isClass="否"
          }
        });
        console.log(this.EmployeeCourseAddDialogModel.tableData)
      },
      EmployeeCourseAddDialog(flag){
        this.Post("QueryStudentByCourseID?courseID="+this.CourseEntity.id,{},response =>{
            if(flag == 1)
            {
              this.EmployeeCourseAddDialogModel.tableData = response.data.data;
              return;
            }
            this.dialogVisible.employeeCourseAdd=true;
            let obj ={};
            this.EmployeeCourseAddDialogModel.value = [];
            this.EmployeeCourseAddDialogModel.employeeData =[];
            if(response.data.data == null || response.data.data.length == 0){
              this.QueryAllEmployee(1);
              return;
            }
            response.data.data.forEach(item =>{
              if(obj[item.studentID])return;
              obj[item.studentID]= true;
              this.EmployeeCourseAddDialogModel.employeeData.push({
                label:item.studentName,
                key:item.studentID
              })
              this.EmployeeCourseAddDialogModel.value.push(item.studentID);

            })
        });
      },
      selectEmployeeDataMethod(query){
        if(!query){
          this.EmployeeCourseAddDialogModel.selectEmployeeData=[];
          return;
        }
        this.AllEmployeeQuery ={
          page: 0,
          grade: "",
          name: ""
      };
      this.AllEmployeeQuery.name = query;
        this.QueryAllEmployee(2);
      },
      filterEmployeeCourseAddDialogMethod(){
        this.QueryAllEmployee(1);
      },
      EmployeeCourseInsert(){
        let arr = [];
        if(this.EmployeeCourseAddDialogModel.value.length != 0){
          this.EmployeeCourseAddDialogModel.value.forEach(i =>{
            let a = {
                  id: 0,
                  studentID: i,
                  courseID: this.CourseEntity.id,
                  coursePID: 0,
                  isClass: "是",
                  description: "",
                  isDelete: "否",
                  img: ""
                }
                arr.push(a);
          })
          this.Post("BatchInsertStudentClass",arr,response =>{
              this.$message({
                        message: '操作成功',
                        type: 'success'
                      });
          });
        }
      },
      CourseAddDialog(){
        this.initCourseEntity();
        this.dialogVisible.courseAdd=true;
      },
      CourseUpdateDialog(entity){
        this.CourseEntity = entity;
        this.dialogVisible.courseUpdate=true;
      },
      InsertCourse(flag){
        this.Post("InsertCourse",this.CourseEntity,response =>{
            if(flag == 'CourseAdd'){
              this.dialogVisible.courseAdd = false;
              this.QueryAllCourse();
            }
            if(flag == "employeeRelationAdd"){
              this.dialogVisible.employeeRelationAdd = false;
              this.QueryEmployeeByPid(this.EmployeeEntity.pid);
            }
        });
      },
      UpdateCourse(){
        this.Post("UpdateCourse",this.CourseEntity,response =>{
            this.dialogVisible.courseUpdate = false;
            this.QueryAllCourse();
        });
      },
      QueryAllCourse(){
        this.Post("QueryAllCourse",this.AllEmployeeQuery,response =>{
            this.tableData = response.data.data
        });
      },
      handleSelect(key) {
        this.index=key;
        this.AllEmployeeQuery={
          page: 0,
          grade: "",
          name: ""
      }
        if(key == 1){
          this.QueryAllEmployee();
        }else if(key == 2){
          this.QueryAllCourse()
        }
      },
      handleClose(key, keyPath) {
        console.log(key, keyPath);
      },
      QueryAllEmployee(flag){
        this.Post("QueryAllEmployee",this.AllEmployeeQuery,response =>{
            if(flag == 1){
              let array = this.EmployeeCourseAddDialogModel.employeeData;
              this.EmployeeCourseAddDialogModel.employeeData = [];
              let obj ={}
              this.EmployeeCourseAddDialogModel.value.forEach(i =>{
                 let item = array.find(n=>n.key == i);
                 if(!item){
                   item = this.EmployeeCourseAddDialogModel.currentValue.find(n=>n.key == i);
                 }
                 if(!item) return;
                 if(!obj[i]){
                   this.EmployeeCourseAddDialogModel.employeeData.push(item);
                   obj[i] = true;
                   if(!this.EmployeeCourseAddDialogModel.currentValue.find(n=>n.key == i)){
                     this.EmployeeCourseAddDialogModel.currentValue.push(item)
                   }
                 }
              })
              response.data.data.forEach(item =>{
                if(obj[item.id])return;
                obj[item.id]= true;
                this.EmployeeCourseAddDialogModel.employeeData.push({
                  label:item.name,
                  key:item.id
                })
              })
            }else if(flag == 2){
              response.data.data.forEach(item =>{
                this.EmployeeCourseAddDialogModel.selectEmployeeData.push({
                  label:item.name,
                  value:item.id
                })
              })
            }
            else{
              this.tableData = response.data.data
            }
        });
      },
      InsertEmployee(flag){
        this.Post("InsertEmployee",this.EmployeeEntity,response =>{
            if(flag == 'employeeAdd'){
              this.dialogVisible.employeeAdd = false;
              this.QueryAllEmployee();
            }
            if(flag == "employeeRelationAdd"){
              this.dialogVisible.employeeRelationAdd = false;
              this.QueryEmployeeByPid(this.EmployeeEntity.pid);
            }
        });
      },
      EmployeeAddDialog(){
        this.initEmployeeEntity();
        this.dialogVisible.employeeAdd=true;
      },
      QueryEmployeeByPid(pid){
        this.Post("QueryEmployeeDetail?pid="+pid,{},response =>{
            this.tableDialog.RelationData = response.data.data
        });
      },
      UpdateEmployeeDialog(entity){
        this.dialogVisible.employeeUpdate = true;
        this.EmployeeEntity=entity;
      },
      RelationEmployeeDialog(entity){
        this.dialogVisible.employeeRelation = true;
        this.initEmployeeEntity();
        this.EmployeeEntity.pid=entity.id;
        this.EmployeeEntity.pidName=entity.name;
        this.QueryEmployeeByPid(entity.id);
      },
      RelationEmployeeDialogAdd(entity){
        this.dialogVisible.employeeRelation = true;
        this.initEmployeeEntity();
        this.EmployeeEntity.pid=entity.id;
        this.EmployeeEntity.pidName=entity.name;
      },
      UpdateEmployee(){
        this.Post("UpdateEmployee",this.EmployeeEntity,()=>{
            this.dialogVisible.employeeUpdate = false;
            this.dialogVisible.employeeRelation = false;
            this.initEmployeeEntity();
            this.QueryAllEmployee();
        });
      },
      QueryDictionaryTable(typeFlag){
        this.Post("QueryDictionaryTable?typeFlag=" + typeFlag,{},()=>{
            this.TabeleEntityList = response.data.data
        })
      },
      initEmployeeEntity(){
        this.EmployeeEntity={
          id: 1,
          pid: 0,
          name: "",
          grade: "",
          phone: "",
          img: "",
          description: ""
        };
      },
      initCourseEntity(){
        this.CourseEntity={
        id: 1,
        pid: 0,
        name: "",
        startTime: "",
        endTime: "",
        dayPeriad: "",
        validTime: "",
        img: "",
        description: "",
        isDelete:0,
        price:200,
      };
      },
      Post(url, obj, callback){
        axios.post(this.hosturl+"api/"+ url, obj, {
          headers: {
            'Content-Type': 'application/json',
          },
        }).then(response => {
          if(callback)callback(response);
        })
        .catch(error => {
          console.error(error);
        });
      }
    },
    created(){
      this.handleSelect(1);
      this.QueryDictionaryTable("GradeFlag");
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
  .el-menu-vertical-demo:not(.el-menu--collapse) {
    width: 200px;
    min-height: 400px;
  }
</style>



